import { motion } from 'framer-motion';
import clsx from 'clsx';

export const Table = ({ 
  columns, 
  data, 
  onSort, 
  sortColumn, 
  sortDirection,
  isLoading,
  rowActions
}) => {
  const tableVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.1
      }
    }
  };

  const rowVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: { opacity: 1, y: 0 }
  };

  if (isLoading) {
    return (
      <div className="w-full space-y-4">
        {[...Array(5)].map((_, i) => (
          <div
            key={i}
            className="h-16 bg-gray-100 dark:bg-dark-bg-secondary animate-pulse rounded-lg"
          />
        ))}
      </div>
    );
  }

  return (
    <div className="overflow-x-auto">
      <motion.table 
        className="min-w-full divide-y divide-gray-200 dark:divide-dark-border"
        variants={tableVariants}
        initial="hidden"
        animate="visible"
      >
        <thead className="bg-gray-50 dark:bg-dark-bg-secondary">
          <tr>
            {columns.map((column) => (
              <th
                key={column.key}
                className={clsx(
                  "px-6 py-3 text-left text-xs font-medium",
                  "text-gray-500 dark:text-gray-400 uppercase tracking-wider",
                  column.sortable && "cursor-pointer hover:text-gray-700 dark:hover:text-gray-200"
                )}
                onClick={() => column.sortable && onSort?.(column.key)}
              >
                <div className="flex items-center space-x-1">
                  <span>{column.label}</span>
                  {column.sortable && sortColumn === column.key && (
                    <span className="ml-2">
                      {sortDirection === 'asc' ? '↑' : '↓'}
                    </span>
                  )}
                </div>
              </th>
            ))}
            {rowActions && <th className="px-6 py-3" />}
          </tr>
        </thead>
        <tbody className="bg-white dark:bg-dark-bg-primary divide-y divide-gray-200 dark:divide-dark-border">
          {data.map((row, index) => (
            <motion.tr
              key={row.id || index}
              variants={rowVariants}
              className="hover:bg-gray-50 dark:hover:bg-dark-bg-secondary transition-colors"
            >
              {columns.map((column) => (
                <td
                  key={column.key}
                  className="px-6 py-4 whitespace-nowrap text-sm text-gray-900 dark:text-gray-300"
                >
                  {column.render ? column.render(row) : row[column.key]}
                </td>
              ))}
              {rowActions && (
                <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                  {rowActions(row)}
                </td>
              )}
            </motion.tr>
          ))}
        </tbody>
      </motion.table>
    </div>
  );
}; 